ento del reparto. ")]),_vm._m(7),_vm._m(8),_c('Separador'),_vm._m(9),_vm._m(10),_c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"col-lg-10 col-12 tarjeta-rosa-linea px-5 py-4"},[_c('div',{staticClass:"row align-items-center justify-content-center"},[_vm._m(11),_c('div',{staticClass:"col-lg-10 col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Para afrontar este reto, la entidad cuenta con el apoyo de expertos en la industria 4.0 como el vicepresidente de transformación digital de la ANDI, Santiago Pinzón, quien se reunió en una videoconferencia con funcionarios de las 33 regionales del SENA para hablar acerca de las necesidades específicas del aparato productivo, de cara a la cuarta revolución industrial "),_c('a',{staticClass:"linkk-content",attrs:{"href":('https://www.sena.edu.co/es-co/Noticias/Lists/Noticias/DispForm.aspx?ID=3611'),"target":"_blank"}},[_c('strong',[_vm._v("(https://www.sena.edu.co/es-co/Noticias/Lists/Noticias/DispForm.aspx?ID=3611)")]),_vm._v(".")])])])])])]),_c('p',{attrs:{"data-aos":"fade"}},[_vm._v("La formación profesional integral que se imparte en el SENA responde a estas novedades y retos, dentro de las estrategias de formación, que faculta el desarrollo del aprendizaje basado en problemas, permitiendo desarrollar en el aprendiz un proceso didáctico propio con una mayor responsabilidad y un rol activo en su aprendizaje, a partir de la planeación, análisis y desarrollo de actividades concretas para proponer soluciones prácticas que contribuyan a la mejora y el desarrollo de su entorno. Es decir, es una estrategia que reconoce que el aprendizaje significativo conduce al aprendiz a un proceso inherente de conocimiento. Es esta misma razón por la cual se entiende que la mejor forma de realizar un proceso simulado es el estudio de casos, entendido este como un proceso hipotético de transformación.")])],1)],1)}
